import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

import Modal from '../../components/Modal';
import Operation from './Operation';
import { updateConfig as updateConfigAction } from './actions';

import '../../App.css';

class Config extends Component {
  state = { show: false };

  handleClick = () => {
    this.setState({ show: true });
  }

  handleClose = () => {
    this.setState({ show: false });
  }

  handleOperationChange = (event, index) => {
    const { config, updateConfig } = this.props;
    const updatedOperations = config.operations;
    const { id, value, type } = event.target;
    const element = id.split('-')[0];
    let setValue;

    switch (type.toLowerCase()) {
      case 'checkbox':
        setValue = !updatedOperations[index][element];
        break;
      case 'number':
        setValue = value.length > 0 ? Number.parseInt(value, 10) : 0;
        break;
      default:
        setValue = value;
        break;
    }

    updatedOperations[index][element] = setValue;

    updateConfig({
      ...config,
      operations: updatedOperations,
    });
  };

  render() {
    const { config } = this.props;
    const { show } = this.state;
    return (
      <Fragment>
        <FontAwesomeIcon icon="cog" onClick={this.handleClick} />
        <Modal show={show} onClose={this.handleClose}>
          <p>Config</p>
          {config.operations.map((operation, index) => (
            <Operation
              index={index}
              key={operation.symbol}
              operation={operation}
              onChange={(event) => { this.handleOperationChange(event, index); }}
            />
          ))}
        </Modal>
      </Fragment>
    );
  }
}

Config.propTypes = {
  config: PropTypes.shape({
    operations: PropTypes.arrayOf(PropTypes.shape()),
    timeBetweenQuestions: PropTypes.number,
  }),
  updateConfig: PropTypes.func,
};

Config.defaultProps = {
  config: {
    operations: [],
    timeBetweenQuestions: 0,
  },
  updateConfig: () => {},
};

const mapStateToProps = (state) => {
  return ({
    config: state.config,
  });
};

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({
    updateConfig: updateConfigAction,
  }, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(Config);
